import { React, useState } from 'react'
import { Link, Redirect, useRouteMatch, useHistory } from 'react-router-dom'

import axios from 'axios'

import logo from '../img/logo2.svg'
import userIcon from '../img/userIcon.svg'

const Navbar = () => {

    let { url } = useRouteMatch();
    let history = useHistory();

    const [State, setState] = useState({
        isOpen: false,
        logOut: false
    });

    const menuClass = `dropdown-menu ${State.isOpen ? " show" : ""}`;

    const handlePress = e => {

        if (e.key === 'Enter') {
            history.push(`${url}/search`);
        }

    }

    const handleLogOut = e => {

        axios.get('/logOut').then(result => {
            console.log(result)

            setState({
                ...State,
                logOut: result.data.status === 200
            })
        });

    }

    // Deesplegar dropdown
    const toggleOpen = e => {

        if (e.target.name === "profile") {
            setState({
                ...State,
                isOpen: !State.isOpen,
            })
        }
    }

    return (
        <div className='container-fluid'>
            <div className='row'>
                <nav class="navbar navbar-light bg-light border border-buttom fixed-top">
                    <div class="container-fluid d-flex justify-content-center">
                        <div className='row d-flex align-items-center'>
                            <div className='col-12 col-sm-4'>
                                <Link class="navbar-brand text-primary d-flex align-items-center"
                                    to={`${url}/Home`}>
                                    <img src={logo} alt='' width='33px' height='33px' />
                                    <span className='ms-2'>Kollections Collage</span>
                                </Link>
                            </div>
                            <div className='col-12 col-sm-4'>
                                <form class="d-flex ps-2 ms-5" onSubmit = {(e) =>{e.preventDefault()}}>
                                    <input
                                        class="form-control"
                                        type="search"
                                        placeholder="Buscar..."
                                        aria-label="Search"
                                        onKeyPress={handlePress}
                                    />
                                </form>
                            </div>
                            <div className='col-12 col-sm-4'>
                                <div className='d-flex ps-5 ms-5 align-items-center'>
                                    <Link className='btn btn-outline-primary ms-4'
                                        to={`${url}/Home`}>
                                        HOME
                                    </Link>
                                    <Link className='btn btn-outline-primary ms-4'
                                        to={`${url}/AddImage`}>
                                        ADD
                                    </Link>
                                    <div className='ms-4 dropdown'>
                                        <img
                                            src={userIcon}
                                            alt=''
                                            role='button'
                                            width='33px'
                                            height='33px'
                                            name='profile'
                                            onClick={toggleOpen}
                                        />
                                        <div className={menuClass}
                                            aria-labelledby='btn-menu'
                                        >
                                            <Link className='dropdown-item' to={`${url}/userprin`}>Perfil</Link>
                                            <button className='dropdown-item'
                                                onClick={handleLogOut}>
                                                Cerrar sesión
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
            {
                State.logOut
                ? <Redirect to = '/' />
                :''
            }
        </div>
    );
}

export default Navbar;